import React from 'react';
import { useSelector } from 'react-redux';
import { makeStyles } from '@material-ui/core/styles';

export interface Props {}

const useStyles = makeStyles((theme) => ({
  listingsContainer: {
    height: '355px',
    overflowY: 'auto',
  },
  listing: {
    borderRadius: '5px',
    backgroundColor: '#eeeeee',
    display: 'flex',
    alignItems: 'center',
    width: '85%',
    height: 75,
    margin: '0px auto',
    marginTop: 15,
    marginBottom: 15,
    paddingLeft: 15,
    transition: 'all .2s linear',
    '&:hover': {
      backgroundColor: '#f8f8f8',
      boxShadow: '0 0 0 1px rgb(0 0 0 / 5%), 0 5px 15px rgb(0 0 0 / 15%)',
    },
  },
  textBox: {
    marginLeft: 10,
  },
  image: {
    width: 30,
    height: 30,
  },
  name: {
    fontSize: 14,
    fontWeight: 600,
    marginRight: 5,
  },
  symbol: {
    fontSize: 10,
  },
}));

export const Popular: React.FC<Props> = () => {
  const classes = useStyles();

  const coins = useSelector((state: any) => state.homePageCoins);
  const data = Object.values(coins);

  return (
    <div className={classes.listingsContainer}>
      {data.length > 0 &&
        data.map((coin: any) => {
          return (
            <div className={classes.listing} key={coin.id}>
              <img
                src={coin.logo}
                alt={`${coin.slug}-logo`}
                className={classes.image}
              />
              <div className={classes.textBox}>
                <span className={classes.name}>{coin.name}</span>
                <span className={classes.symbol}>{coin.symbol}</span>
              </div>
            </div>
          );
        })}
    </div>
  );
};

export default Popular;
